/*
main font size: 22px 
title font size: 55px;
icon font size: 40px;
main color: azure 
btn font size: 20px*/
.btn{
    border-radius: 50px;
    height: 60px;
    background-color: azure;
    padding: 10px 20px;
    font-size: 20px;
    cursor: pointer;
    &:hover {
        background-color: #1e293b;
        color: #fff;
      }
}
@font-face {
	font-family: 'Playfair Display';
	src: url('../assets/fonts/Playfair_Display/PlayfairDisplay-VariableFont_wght.ttf') format('truetype');
	font-weight: 400;
	font-style: normal;
}
body{
    background: rgb(0, 0, 0);
    /*background: linear-gradient(135deg, rgb(0, 0, 0) 0%, rgba(112,143,150,1) 33%, rgb(64, 32, 51) 66%, rgb(98, 13, 13) 100%);*/
    background: linear-gradient(135deg, rgb(0, 0, 0) 0%, rgba(112,143,150,1) 33%, rgb(205, 221, 230) 66%, rgb(231, 196, 127) 100%);
}
.fa{
    font-size: 40px;
}
header{
    width: 100%;
    padding: 2vw 6vw;
    box-sizing: border-box;
    background-color: azure;
    hr{
        margin: 20px 0px;
    }
    .header-nav{
        width: 100%;
        
        .header-nav-top{
            width: 100%;
            display: flex;
            justify-content: space-between;
            box-sizing: border-box;
            align-items: center;
            h1{
                margin: 0px;
                font-size: 36px;
            }
            .header-logo{
                font-family: "Playfair Display";
                font-weight: 500;
                
            }
            .header-nav-top-icons{
                display: flex;
                gap: 20px;
            }
        }
        .header-nav-bottom{
            width: 100%;
            display: flex; 
            justify-content: center;
            gap: 2vw;
            font-size: 22px;
        }
        
    }
}


.mainpage{
    width: 100%;
    height: 100%;
    box-sizing: border-box;
    .mainpage-text{
        z-index: 6;
        height: 100%;
        width: 100%;
        position: absolute;
        padding: 40% 6vw 50% 60% ;
        font-family: "Playfair Display";
        color: azure;
        font-size: 54px;
        font-weight: 300;
        box-sizing: border-box;
    }
    .mainpage-pic{
        position: relative;
        height: 100%;
        width: 100%;
    }
}



.occasion{
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(5, 1fr);
    
    box-sizing: border-box;
    overflow: hidden;
    padding: 4vw 4vw 7vw 4vw;
    margin: 2vw;
    row-gap: 4vw;
    height: 100%;
    font-size: 26px;
    font-weight: 500;
    background: rgba(255, 255, 255, 0.2);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    border: 1px solid rgba(255, 255, 255, 0.3);

    p{
        margin: 0px;
    }
    
    .occasion2{
        cursor: pointer;
        box-sizing: border-box;
        width: 13vw;
        height: 13vw;
        text-align: center;
        transition: all 0.3s ease;
        &:hover {
            transform: scale(1.1);}
        img{
            object-fit: cover;
            width: 100%;
            height: 100%;
            border-radius: 50%;
            margin: 0%;
        }
    }
  
}

.popularProducts-title{
    padding: 0px 0px 0px 6vw;
    font-family: 'Playfair Display'; 
    font-size: 55px;
}
.popularProducts {
	padding: 0px 6vw;
    display: grid;
    grid-gap: 16px;
    grid-template-columns: repeat(auto-fill,minmax(22%,1fr));
    
    row-gap: 40px;
    .popularProducts-product{
        position: relative;
        background: rgba(255, 255, 255, 0.2);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5px);
        border: 1px solid rgba(255, 255, 255, 0.3);
        
        &:hover .popularProducts-slider {
            transform: translateY(-50px);
        }
        .popularProducts-slider{
            position: absolute;
            width: 100%;
            font-size: 22px;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: flex-start;
            padding-top: 12px;
            box-sizing: border-box;
            background: rgba(255, 255, 255, 0.2);
            border-radius: 16px;
            box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
            backdrop-filter: blur(5px);
            border: 1px solid rgba(255, 255, 255, 0.3);
            transition: all 0.3s ease;
            height: 80px;
            transform: translateY(0);
            div {
                display: flex;
                align-items: center;
                gap: 5px;
            }
            p{
                margin: 0px;
            }
            
        }
        .popularProducts-item {
            position: relative;
            z-index: 60;
            display: flex;
            flex-direction: column;
            align-items: center;
            height: 65vh;
            
            .popularProducts-item-description{
                text-align: center;
                padding: 2vw;
                h3{
                    margin: 0px;
                }
                p{
                    font-size: 30px;
                    margin: 10px;
                }
                .popularProducts-button{
                    width: 100%;
                    display: flex; 
                    justify-content: center;
                    gap: 2vw;
                    font-size: large;
                    
                }
            }
            img{
                object-fit: cover;
                border-radius: 16px;
                width: 100%;
                height: 100%;
                overflow: hidden;
            }
        }
        .popularProducts-item-more-box{
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            height: 100%;
            .popularProducts-item-more{
                border-radius: 16px;
                width: 80%;
                height: 80%;
                margin-top: 10%;
                overflow: hidden;
                display: flex;
                justify-content: center;
                background-color: aliceblue;
                flex-direction: column;
                align-items: center;
                box-sizing: border-box;
                h1{
                    font-size: 65px;
                    margin: 0px;
                }
                p{
                    font-size: 40px;
                    margin: 0px;
                    text-align: center;
                }
                
            }
            .popularProducts-item-more-description{
                display: flex;
                flex-direction: row;
                align-items: center;
                justify-content: space-between;
                gap: 10px;
                height: 50%;
                width: 80%;
                h3{
                    font-size: 35px;
                    margin: 0px;
                    font-family: 'Playfair Display';
                    width: 60%;
                }
                .popularProducts-item-more-description-btn{
                    height: 70px;
                    width: 70px;
                    font-size: 35px;
                    display: flex;
                    text-align: center; 
                    align-items: center;
                }
            }
        }
    } 
}
.priceSort{
    display: flex;
    width: 100%;
    justify-content: center;
    gap: 50px;
    .priceSort-item{
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        font-size: 22px;
        padding: 3vw;
        border-radius: 50px;
        padding: 10px 20px;
        cursor: pointer;
        background: rgba(255, 255, 255, 0.2);
        border-radius: 16px;
        box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
        backdrop-filter: blur(5px);
        border: 1px solid rgba(255, 255, 255, 0.3);
        h2{
            margin: 0px;
        }
        transition: all 0.3s ease;
        &:hover {
            transform: scale(1.1);}
        }
}
.recommendation{
    width: 100%;
    padding: 6vw;
    box-sizing: border-box;
    h1{
        
        margin: 0px 0px 80px 0px;
        font-size: 55px;
        font-family: 'Playfair Display'; 
    }
    .recommendation-points{
        display: flex;
        flex-wrap: wrap;
        gap: 55px;
        justify-content: center;
        h2{
            font-family: 'Playfair Display'; 
            font-size: 36px;
            text-align: center;
        }
        p{
            font-size: 22px;
        }
        .recommendation-points-item{
            flex: 0 0 30%;
            display: flex;
            flex-direction: column;
            align-items: center;
        }
    }
}

footer{
    width: 100%;
    padding: 2vw 6vw;
    box-sizing: border-box;
    background-color: azure;
    .footerLinks{
        display: flex;
        justify-content: center;
        gap: 20px;
        font-size: 22px;

    }
    .footer-logo{
        font-family: "Playfair Display";
        font-weight: 500;
        text-align: center;
        margin: 25px;
        font-size: 55px;
    }
    hr{
        margin: 20px 0px;
    }
    .footer-icons{
        display: flex;
        justify-content: center;
        gap: 20px;
        margin: 25px;
    }
}
